// offset overlay: https://codepen.io/virtual/pen/XWXPVMR
$hero-offset: 20px;


@media(min-width: $screen-lg) {
  .homepage .content-1 {
    padding-bottom: 1.5rem;
  }
  .homepage .content-2 { 
    padding-top: 0;
  } 
}
.homepage .testimonial-slider-container {
  margin-top: 2rem;
  // padding-right: 1.75rem;
  // padding-left: 1.75rem;
} 


.hero-bg-container {
  margin-right: 0;
  margin-left: 0;
  padding-top: 2rem;
  .hero-offset-bg {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: $hero-offset;
    padding-top: 0;
  }
  .hero-content {
    z-index: 2;
    margin-top: $hero-offset * -1;
    padding: 2.8125rem 1.5rem 2rem; 
    text-align: center;
    word-wrap: break-all; 
    .btn {
      width: 100%;
    }
  }
  .hero-bg-offset-image {
    margin-bottom: $hero-offset * -2;
    // box-shadow: 0 0 1px #f00;
    img, picture {
      width: 100%;
      margin: 0 0 0 auto;
    }
  }
  .hero-cta {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
  }
  .hero-cta-sub {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
  }
}
@media (min-width: $screen-sm) {
  .hero-bg-container .hero-content .btn {
    width: auto;
  }
}
@media (min-width: $screen-md) {
  .hero-bg-container {
    // margin-right: $hero-offset * -1;
    // margin-left: $hero-offset * -1;
    padding-top: 5.25rem;
    .hero-offset-bg {
      flex-direction: row;
      padding-right: 0;
    }
    .hero-content {
      flex-basis: 48%;
      padding: 64px 32px; 
      text-align: left;      
    }  
    .hero-bg-offset-image {
      position: absolute;
      top: -60px;
      right: 0;
      z-index: 1;
      display: flex;
      align-items: center;
      width: 80%;
      height: calc(100% + 80px);
      margin-bottom: 0;      
      padding-right: $hero-offset / 2;
      overflow: hidden;
      // margin:0 auto;
      img, picture {
        width: 100%;
        margin: 0 0 0 auto;
      }
    }
  }  
  .hero-cta {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .hero-cta-sub {
    margin-top: 1.5rem;
    margin-bottom: 2.5rem;
  }
}
@media(max-width: $screen-lg - 1px) {
  .hero-bg-container {
    .hero-offset-bg {
      padding: $hero-offset / 2;
    }
    .hero-bg-offset-image {
      margin-bottom: $hero-offset / 2 * -2;
    }
  }
}

@media (min-width: $screen-lg) {
  .hero-bg-container {
    width: calc(100% + 32px);
    margin-right: $hero-offset * -1;
    margin-left: $hero-offset * -1;
    .hero-bg-offset-image {
      padding-right: $hero-offset;
    }
  }
.hero-bg-container .hero-content {
    flex-basis: 43%;
    padding: 72px 42px; 
  }  
}
@media (min-width: $screen-xl) {
.hero-bg-container .hero-content {
    flex-basis: 38%;
    padding: 87px 42px 87px 55px; 
  }  
}

.grad-cap-coin {
    animation-duration: 3s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}
.grad-cap-icon:hover .grad-cap-coin {
    animation-name: bounce-3; 
}
@keyframes bounce-3 {
  0%   { transform: translateY(0); }
  30%  { transform: translateY(2px); }
  40%  { transform: translateY(0); }
  100% { transform: translateY(0); }
}



